___CSS_LOADER_EXPORT___.push([module.id, `body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f4f4f4;
    display: grid;
    grid-template-columns: 1fr 4fr;
    min-height: 150vh;
    gap: 20px;
}

.container {
    max-width: 800px;
    margin: 20px auto;
    padding: 20px;
    background-color: #fff;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

h1 {
    text-align: center;
    color: #ff4081; /* Pink color */
    font-size: 35px;
}

.content {
    display: flex;
}

.todo-sidebar {
    width: 250px;
}

.todo-form {
    margin-bottom: 20px;
    display: flex;
    flex-direction: column;
}

.todo-main {
    flex: 1;
}

.todo-input {
    width: calc(100% - 80px);
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px 0 0 5px;
    font-size: 16px;
}

.todo-btn {
    padding: 10px 20px;
    border: none;
    border-radius: 0 5px 5px 0;
    background-color: #ff4081; /* Pink color */
    color: #fff;
    font-size: 16px;
    cursor: pointer;
}

.todo-btn:hover {
    background-color: #ff005b; /* Darker pink on hover */
}

.todo-list {
    list-style-type: none;
    padding: 0;
}

.todo-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    border-bottom: 1px solid #ccc;
}

.todo-item:last-child {
    border-bottom: none;
}

.delete-btn {
    background-color: #ff005b; /* Darker pink color */
    color: #fff;
    border: none;
    padding: 5px 10px;
    border-radius: 5px;
    cursor: pointer;
}

.delete-btn:hover {
    background-color: #dc004e; /* Slightly lighter pink on hover */
}
